'use client';
import { useEffect, useState } from 'react';
import { useToast } from '@/hooks/use-toast';
import { jsPDF } from 'jspdf';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Skeleton } from '@/components/ui/skeleton';
import { EventCategory } from '@/types/type';
import { useDebounce } from 'use-debounce';
import { motion, AnimatePresence } from 'framer-motion';
import { events } from '@/constants/events';
import {
  ChevronLeft,
  ChevronRight,
  Clock3,
  DollarSign,
  Download,
  Flame,
  Gift,
  Rocket,
  School2Icon,
  Search,
} from 'lucide-react';
import EventCard from '@/components/ui/EventCard';
import Link from 'next/link';
import UnregisterConfirmationDialog from '@/components/dialog/UnregisterConfirmationDialog';

const filters = [
  { label: 'All', icon: School2Icon },
  { label: 'Upcoming', icon: Flame },
  { label: 'Past', icon: Clock3 },
  { label: 'Free', icon: Gift },
  { label: 'Price', icon: DollarSign },
];

const UserDashboard = () => {
  const { toast } = useToast();
  const [event, setEvents] = useState<EventCategory[]>([]);
  const [activeFilter, setActiveFilter] = useState<string>('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [debouncedQuery] = useDebounce(searchQuery, 300);
  const [currentPage, setCurrentPage] = useState(1);
  const [eventsPerPage] = useState(6);
  const [selectedEvent, setSelectedEvent] = useState<EventCategory | null>(
    null
  );
  const [isUnregisterModalOpen, setIsUnregisterModalOpen] = useState(false);
  const [isStatusDialogOpen, setIsStatusDialogOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  const generateInvoice = (event: EventCategory) => {
    const doc = new jsPDF();

    doc.setFontSize(16);
    doc.text('Booking Invoice', 20, 20);

    doc.setFontSize(12);
    doc.text(`Student Name: Test User`, 20, 40);
    doc.text(`Student Email: test@example.com`, 20, 50);

    doc.text(`Event Name: ${event.title}`, 20, 70);
    doc.text(`Location: ${event.location}`, 20, 80);
    doc.text(`Date: ${new Date(event.date).toLocaleDateString()}`, 20, 90);

    doc.text(`Booking ID: #${event.registeringId || '123456'}`, 20, 110);
    doc.text('Thank you for booking with us!', 20, 130);

    doc.save(`Invoice-${event.title}.pdf`);
  };

  // Fillter events
  const filteredEvents = (events || []).filter((eve) => {
    const matchesSearch = eve.title
      .toLowerCase()
      .includes(debouncedQuery.toLowerCase());
    const matchesFilter =
      activeFilter === 'all' ||
      activeFilter === 'upcoming' ||
      activeFilter === 'past' ||
      (activeFilter === 'free' && !eve.prices) ||
      (activeFilter === 'price' && eve.prices);

    return matchesSearch && matchesFilter;
  });

  const indexOfLastEvent = currentPage * eventsPerPage;
  const indexOfFirstEvent = indexOfLastEvent - eventsPerPage;
  const currentEvents = filteredEvents.slice(
    indexOfFirstEvent,
    indexOfLastEvent
  );
  const totalPages = Math.ceil(filteredEvents.length / eventsPerPage);

  const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

  const fetchEvents = async () => {
    setLoading(true);
    const type = activeFilter === 'all' ? '' : activeFilter;
    try {
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/users/me/registered-events?page=${currentPage}&perPage=${eventsPerPage}&type=${type}`,
        {
          method: 'GET',
          credentials: 'include',
        }
      );
      const data = await res.json();
      setEvents(data.events);
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error?.message || 'Failed to fetch events.',
        variant: 'destructive',
      });
    } finally {
      setLoading(false);
    }
  };
  const confirmUnregister = async (id:string) => {
    if (!selectedEvent) return;

    try {
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/users/me/events/${id}/unregister`,
        {
          method: 'DELETE',
          credentials: 'include',
          headers: {
            'Content-Type': 'application/json',
          },
        }
      );

      setEvents((prev) => prev.filter((user) => user.id !== selectedEvent.id));
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error?.message || 'An unexpected error occurred.',
        className: 'bg-red-500 text-white border-0',
      });
    }

    setIsUnregisterModalOpen(false);
    setSelectedEvent(null);
  };

  useEffect(() => {
    //fetchEvents();
  }, [activeFilter, currentPage]);

  return (
    <section className='px-12 py-12'>
      <h1 className='text-2xl font-bold mb-6 text-center text-purple-800'>
        🎉 My Booked Events
      </h1>
      <div className='flex flex-col md:flex-row gap-4 justify-between items-center mb-6'>
        <div className='relative w-full md:w-1/3 my-6'>
          <div className='absolute inset-y-0 left-0 pl-3 py-3 flex items-center pointer-events-none'>
            <Search className='h-5 w-5 text-purple-400' />
          </div>
          <Input
            placeholder='Search events...'
            className='pl-10 pr-4 py-2 rounded-full border border-purple-300 focus:ring-2 focus:ring-purple-500 focus:border-transparent w-full'
            value={searchQuery}
            onChange={(e) => {
              setSearchQuery(e.target.value);
              setCurrentPage(1);
            }}
          />
        </div>

        <div className='flex flex-wrap gap-2 justify-center'>
          {filters.map(({ label, icon: Icon }) => {
            const key = label.toLowerCase();
            const isActive = activeFilter === key;
            return (
              <motion.button
                key={key}
                whileHover={{ scale: 1.05 }}
                onClick={() => setActiveFilter(key)}
                className={`flex items-center gap-2 px-4 py-2 rounded-full transition-colors
                                ${
                                  isActive
                                    ? 'bg-purple-600 text-white'
                                    : 'bg-gray-200 text-purple-700 hover:bg-purple-100'
                                }`}
              >
                <Icon size={18} /> {label}
              </motion.button>
            );
          })}
        </div>
      </div>
      {loading ? (
        <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6'>
          {[...Array(3)].map((_, i) => (
            <Skeleton
              key={i}
              className='h-[300px] w-full rounded-xl'
            />
          ))}
        </div>
      ) : filteredEvents.length === 0 ? (
        <p className='text-center text-gray-500 mt-10'>
          You haven't booked any events yet.
        </p>
      ) : (
        <AnimatePresence>
          <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6'>
            {currentEvents.map((event, index) => (
              <Link
                href={`events/${event.id}`}
                key={index}
              >
                <EventCard
                  key={event.id}
                  event={event}
                  className=' relative'
                >
                  <div className='p-4 space-y-2'>
                    <h3 className='text-xl font-semibold text-purple-700'>
                      {event.title}
                    </h3>
                    <p className='text-sm text-gray-600'>{event.description}</p>
                    <p className='text-sm text-gray-500'>
                      📅 {new Date(event.date).toLocaleDateString()}
                    </p>
                    <p className='text-sm text-gray-500'>📍 {event.location}</p>
                    {event.prices ? (
                      <>
                        <div className='flex justify-between items-center text-center mt-4'>
                          <Button
                            onClick={() => generateInvoice(event)}
                            className='mt-3 bg-purple-500 text-white'
                          >
                            <Download size={16} /> Download Invoice
                          </Button>
                          <Button
                            variant='outline'
                            size='sm'
                            className='text-green-600 hover:bg-green-50'
                            onClick={() => {
                              setSelectedEvent(event);
                              setIsStatusDialogOpen(true);
                            }}
                          ></Button>
                          {/* الأسعار */}

                          <div className='flex flex-wrap gap-2'>
                            <span className='bg-purple-50 text-purple-800 font-medium px-3 py-1 rounded-full text-sm shadow-sm'>
                              {event.prices} UE
                            </span>
                          </div>
                        </div>
                        <motion.div
                          initial={{ scale: 0, rotate: -15 }}
                          animate={{ scale: 1, rotate: -15 }}
                          className='absolute top-2 left-2 px-3 py-1 bg-red-500/90 rounded-md
                      backdrop-blur-sm transform skew-x-12 shadow-lg'
                        >
                          <span className='text-white font-bold italic'>
                            PAID
                          </span>
                        </motion.div>
                      </>
                    ) : (
                      <motion.div
                        initial={{ scale: 0, rotate: -15 }}
                        animate={{ scale: 1, rotate: -15 }}
                        className='absolute top-2 left-2 px-3 py-1 bg-green-500/90 rounded-md
          backdrop-blur-sm transform skew-x-12 shadow-lg'
                      >
                        <span className='text-white font-bold italic'>
                          FREE
                        </span>
                      </motion.div>
                    )}

                    <motion.div
                      initial={{ scale: 0 }}
                      animate={{ scale: 1 }}
                      className='absolute top-2 right-2 p-2 bg-purple-600/80 rounded-full
                                    backdrop-blur-sm'
                    >
                      <Rocket
                        size={18}
                        className='text-cyan-400'
                      />
                    </motion.div>
                  </div>
                </EventCard>
              </Link>
            ))}
          </div>
        </AnimatePresence>
      )}
      {/* Pagination Controls */}
      {filteredEvents.length > eventsPerPage && (
        <div className='flex justify-center mt-12'>
          <nav className='flex items-center gap-2'>
            <button
              onClick={() => paginate(Math.max(1, currentPage - 1))}
              disabled={currentPage === 1}
              className={`p-2 rounded-full ${
                currentPage === 1
                  ? 'opacity-50 cursor-not-allowed'
                  : 'hover:bg-purple-500/20'
              }`}
            >
              <ChevronLeft className='text-purple-400' />
            </button>

            {Array.from({ length: totalPages }).map((_, index) => (
              <button
                key={index}
                onClick={() => paginate(index + 1)}
                className={`w-10 h-10 rounded-full flex items-center justify-center
                                                   ${
                                                     currentPage === index + 1
                                                       ? 'bg-purple-600 text-white'
                                                       : 'text-purple-400 hover:bg-purple-500/20'
                                                   } `}
              >
                {index + 1}
              </button>
            ))}

            <button
              onClick={() => paginate(Math.min(totalPages, currentPage + 1))}
              disabled={currentPage === totalPages}
              className={`p-2 rounded-full ${
                currentPage === totalPages
                  ? 'opacity-50 cursor-not-allowed'
                  : 'hover:bg-purple-500/20'
              } `}
            >
              <ChevronRight className='text-purple-400' />
            </button>
          </nav>
        </div>
      )}{' '}
    </section>
  );
};

export default UserDashboard;
